<template>
  <d-container fluid class="main-content-container px-4 pb-4">
    <!-- Page Header -->
    <d-row no-gutters class="page-header py-4">
      <!-- Page Header - Page Title -->
      <d-col col sm="4" class="text-center text-sm-left mb-4 mb-sm-0">
        <h3 class="page-title">
          {{ title }}
        </h3>
      </d-col>

      <!-- Page Header - Datepicker -->
      <d-col lg="2" sm="2" class="offset-sm-6 d-flex align-items-right">
        <Calendar @change="setTime" />
      </d-col>
    </d-row>

    <!-- List File Manager -->
    <v-client-table
      class="dataTables_wrapper"
      :data="tableData"
      :columns="columns"
      :options="clientTableOptions"
    >
      <span slot="date" slot-scope="props">
        {{ props.row.date | dateFormat }}
      </span>
    </v-client-table>
  </d-container>
</template>

<script>
import Vue from "vue";
import { ClientTable, Event } from "vue-tables-2";
import Calendar from "./Calendar";
import moment from "moment";
import customFilters from "@/data/dataFilter";
import "@/assets/scss/vue-tables.scss";
Vue.use(ClientTable);

export default {
  components: {
    Calendar
  },
  filters: {
    dateFormat: function(value) {
      return moment(value).format("DD MM YYYY");
    }
  },
  props: {
    title: {
      type: String,
      required: true
    },
    tableData: {
      type: Array,
      required: true,
      default() {
        return [];
      }
    },
    columns: {
      type: Array,
      required: true,
      default() {
        return [];
      }
    },
    headings: {
      type: Object,
      required: true,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      /**
       * Vue Tables Configuration Options
       * @see https://github.com/matfish2/vue-tables-2
       */
      clientTableOptions: {
        perPage: 10,
        recordsPerPage: [10, 25, 50, 100],
        headings: this.headings,
        skin: "transaction-history table dataTable",
        sortIcon: {
          base: "fas float-right mt-1 text-muted",
          up: "fa-chevron-down",
          down: "fa-chevron-up"
        },
        texts: {
          count:
            "Показывать {from} от {to} из {count} записей|{count} Записей|Одна запись",
          filterPlaceholder: "",
          limit: "Показать"
        },
        pagination: {
          edge: true,
          nav: "scroll"
        },
        dateColumns: ["date"],
        dateFormat: "DD MM YYYY",
        customFilters: customFilters(moment)
      }
    };
  },
  methods: {
    setTime(val) {
      Event.$emit("vue-tables.filter::dateRange", val);
    }
  }
};
</script>

<style lang="scss">
@media (max-width: 576px) {
  .VueTables .VuePagination nav {
    flex-direction: column;
  }

  .VueTables .VuePagination ul.pagination {
    width: 100% !important;
    justify-content: center;
  }

  .VueTables .VuePagination__count {
    width: 100% !important;
    max-width: inherit !important;
    margin-top: 15px !important;
  }
}
</style>
