<template>
  <DataTable
    title="Статистика"
    :columns="columns"
    :table-data="tableData"
    :headings="headings"
  />
</template>

<script>
import Vue from "vue";
import DataTable from "./DataTable";
import transactionHistoryData from "@/data/transaction-history-data.json";

export default {
  components: {
    DataTable
  },
  data() {
    return {
      columns: ["total", "price", "date"],
      headings: {
        total: "Всего запросов",
        price: "Стоимость",
        date: "Дата"
      },
      tableData: transactionHistoryData
    };
  }
};
</script>
