<template>
  <d-form>
    <d-row>
      <d-col sm="12" lg="9">
        <p>
          <strong
            >Прежде чем подавать заявку в поддержку, ознакомьтесь со справочной
            информацией</strong
          >
        </p>
      </d-col>
    </d-row>
    <d-row class="mb-4">
      <d-col sm="12" lg="2">
        <d-link>FAQ</d-link>
      </d-col>
      <d-col sm="12" lg="5">
        <d-link>Документация к API</d-link>
      </d-col>
    </d-row>
    <d-form-row>
      <d-col md="12" class="form-group">
        <label for="heading">Заголовок</label>
        <d-input
          id="heading"
          type="text"
          placeholder="Короткое описание вашего обращения"
        />
      </d-col>
    </d-form-row>

    <d-form-row>
      <d-col md="12" class="form-group">
        <label for="description">Описание</label>
        <d-textarea
          id="description"
          style="min-height: 100px;"
          name="description"
        />
      </d-col>
    </d-form-row>

    <d-form-row>
      <d-col md="12" class="form-group">
        <label for="file">Загрузить файл</label>
        <div class="dropzone-wrapper">
          <VueDropzone id="file" class="dropzone" :options="dropzoneOptions" />
        </div>
      </d-col>
    </d-form-row>
    <d-button type="submit">
      Отправить
    </d-button>
    <d-button theme="danger" class="ml-3" @click.prevent="$emit('close')">
      Отмена
    </d-button>
  </d-form>
</template>

<script>
import VueDropzone from "vue2-dropzone";
export default {
  name: "TheSupport",
  components: {
    VueDropzone
  },
  data() {
    return {
      topic: 1,
      dropzoneOptions: {
        url: "https://yout-api-here/post",
        thumbnailWidth: 150,
        dictDefaultMessage: "Перетащите файл сюда"
      }
    };
  }
};
</script>
