<template>
  <DataTable
    title="История операций"
    :columns="columns"
    :table-data="tableData"
    :headings="headings"
  />
</template>

<script>
import Vue from "vue";
import DataTable from "./DataTable";
import operationHistoryData from "@/data/operation-history-data.json";

export default {
  components: {
    DataTable
  },
  data() {
    return {
      columns: ["date", "income", "expense", "operation"],
      tableData: operationHistoryData,
      headings: {
        date: "Дата",
        income: "Приход",
        expense: "Расход",
        operation: "Операция"
      }
    };
  }
};
</script>
